<script>
	import uCharts from '@/uni_modules/qiun-data-charts/js_sdk/u-charts/config-ucharts.js';
	const APP = getApp();
	import headerNavigation from "@/components/header/header.vue";
	import {formatTime} from "@/utils/utils.js";
	import {
		getSalesAnalysis,
		getSalesInfoCharts
	} from '../../utils/http.js';
	export default {
		components: {
			headerNavigation
		},
		data() {
			return {
				headerHeight: 0,
				isBubble: false,
				imgUrl: APP.globalData.imgUrl,
				timeValue: formatTime(new Date().setDate(new Date().getDate() - 1)),
				selectType: '1',
				timeType: 'day',
				x: 0,
				y: 0,
				current: 3,
				titleList:[
					{title: '部门占比', key: 3},
					{title: '客户占比', key: 2},
					{title: '城市占比', key: 1},
				],
				cityList: [],	
				customerList: [],	
				depList: [],
				topList: [],
				initTop: {},
				opts: {
					title: {
						name: "80%",
						fontSize: 12,
						color: "#3C61DF",
					},
					
					extra: {
						arcbar: {
							type: "circle",
							lineCap: "butt"
						}
					}
				}, //进度条样式
				chartData: {}, //进度条数据
			    ringChartData: {}, //环形图数据
				ringOpts: {
					rotate: false,
					rotateLock: false,
					dataLabel: false,
					enableScroll: false,
					// padding: [30, 30, 30, 30],
					color: ["#2583E3", "#DF8A0D", "#28AA5C", "#8A8DE4"],
					fontSize: 12,
					legend: {
						show: false
					},
					title: {
						name: "部门占比",
					},
					subtitle: {
						name: "",
						fontSize: 10,
						color: "#111A33"
					},
					extra: {
						ring: {
							borderWidth: 1,
							ringWidth: 35
						},
						pie: {
							border: false
						}
					}
				},
				minOpts: {
					update: true,
					animation: false,
					duration: 0,
					xAxis:{
						axisLine: false,
						gridColor: '#f1f1f1',
					},
					legend: {
						show: true, 
						itemGap: 5,
					},
					yAxis: {
						// gridType:'dash',
						gridColor: '#f1f1f1',
						data: [{
								position: "right",
								title: "",
								axisLine: false,
								fontColor: "#999999",
								fontSize: 10,
								titleFontSize: 10,
								titleFontColor: "#999999",
								min: 0,
								titleOffsetX: 5,
								titleOffsetY: -5
							},
							{
									position: "left",
									title: "GMV",
									// textAlign: "left",
									axisLine: false,
									fontColor: "#999999",
									fontSize: 10,
									titleFontSize: 10,
									titleFontColor: "#999999",
									min: 0,
									titleOffsetX: -10,
									titleOffsetY: -5
								},
						]
					}
				}, //圆柱形样式
				minChartData: {}, //圆柱形数据
			}
		},
		onLoad() {
			
		},
		computed:{
			statusBarClientRectTop(){
				var that = this;
				// #ifdef H5
				return '0px'
				// #endif
				
				// #ifdef APP-PLUS
				return (uni.getSystemInfoSync().statusBarHeight + 135) *2 + 'rpx'
				// #endif
						
				// #ifdef MP-WEIXIN
				  return `${
					uni.getMenuButtonBoundingClientRect().top +2
				  }px`;
				  // #endif 
							    
			},
		},
		methods: {
			getIndex(e) {
				let categories = uCharts.option[e.id].categories;
				let series = uCharts.option[e.id].series;
				let index = e.currentIndex;
				var x = e.event.x
				var y = e.event.y
				// if(x>=60){
				// 	x = e.event.x - 50
				// }
				uCharts.instance[e.id].showToolTip({
				   changedTouches: [{x: x, y: y}], // changedTouches:点击的坐标值, 可自定义位置[{x: 100, y: 10}]
				},{
					index: e.currentIndex,
					"formatter":function(item, category, index, opts){
					  return '自定义//'+item.name + '//'+'GMV '+item.value +'('+ ((item._proportion_)*100).toFixed(2) +'%)'
					},
				});
				// this.ringChartData={}
			},
			// 卡片信息
			getTopInit(){
				const data ={
					'data-type': this.timeType,
					'data-value': this.timeValue,
				}
				getSalesAnalysis(data).then(res => {
					this.initTop = res.data
					let list = {
						series: [{
							name: "达成率",
							color: "#3C61DF",
							data: res.data.sales / res.data.target
						}]
					};
					this.opts.title.name = ((res.data.sales / res.data.target)*100).toFixed(2) + '%'
					this.chartData = JSON.parse(JSON.stringify(list))
				})
			},
			// 当前选中的卡片数据
			select(val) {
				const title = (val == '1' && 'GMV') || (val == '2' && '订单量')|| (val == '3' && '总件数')
				|| (val == '4' && '下单客户')|| (val == '5' && '客单价')|| (val == '6' && '件单价')|| (val == '7' && '退货')
				this.selectType = val
				this.minOpts.yAxis.data[1].title = title
				this.getCustomSalesInfoCharts()
			},
			// header回调数据
			selectChange(data) {
				this.timeValue = data.timeValue;
				this.timeType = data.timeType;
				this.current = 3
				this.ringOpts.title.name ="部门占比"
			    this.getTopInit()
				this.getCustomSalesInfoCharts()
			},
			getCustomSalesInfoCharts() {
				let that = this;
				that.$showLoading()
				
				const data ={
					'data-type': this.timeType,
					'data-value': this.timeValue,
					'data-form': this.selectType,
				}
				getSalesInfoCharts(data).then(res => {
					uni.hideLoading();
					res.data.trend_data.series.forEach(item => {
						if(item.type != 'line'){
							item.index = 1
						}
					})
					this.minChartData = res.data.trend_data;
					this.topList = res.data.sp_report_dep_names
					this.cityList = res.data.ring_city_list
					this.customerList = res.data.ring_customer_list
					this.depList = res.data.sp_report_dep_names
					this.getRingData()
				})
			},
			//格式化数据
			currency(v){
				let val = parseFloat(Number(v) || 0)
				val = isNaN(val) ? undefined : val
				
				return Number(val.toFixed(0)).toLocaleString()
			},
			onBubble(){
				this.isBubble = !this.isBubble
			},
			onTab(key){
				this.current = key
				this.topList = (key == '1' && this.cityList) || (key == '2' && this.customerList) || (key == '3' && this.depList)
				this.ringOpts.title.name = (key == '1' && "城市占比") ||  (key == '2' &&"客户占比") ||  (key == '3' &&"部门占比")
				this.getRingData()
			},
			// 环形图数据
			getRingData() {
				let that = this;
				let customData = []
				for (var i = 0; i < this.topList.length; i++) {
					let parama = {};
					parama.name = this.topList[i].name
					parama.value = this.topList[i].sales
					// parama.labelText= this.topList[i].name + this.topList[i].sales_rate
				    parama.percent = this.topList[i].sales_rate
					parama.chargeback_num = this.topList[i].chargeback_num
					customData.push(parama);
				}
				let res = {
					series: [{
						data: customData
					}]
				};
				this.ringChartData = JSON.parse(JSON.stringify(res));
			}
		}
	}
</script>

